<template>
  <div>
    <el-row type="flex" justify="end" align="middle" style="padding: 20px">
      <el-col :span="2">
        <router-link :to="{ path: 'setQuestion'}">
          <el-button type="primary" size="large" style="postio"></el-button>
        </router-link>
      </el-col>
    </el-row>
    <el-table
      :data="this.QuestionnaireTemplate.list"
      border
      style="width: 100%;">
      <el-table-column
        label=""
        width="300">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.topic.length }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <router-link :to="{ path: 'setQuestion', query: { questionnaireTemplateId: scope.row._id}}">
            <el-button size="small"></el-button>
          </router-link>
          <el-button type="danger" size="small" @click="remove(scope.row._id, scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'SetQuestionnaireTemplate',
    data(){
      return {
        fullscreenLoading: false
      }
    },
    methods: {
      ...mapActions(['removeTemplateById']),
      remove(templateId, index) {
        this.removeTemplateById({templateId: templateId, index: index}).then((error) => {
          if(error){
            this.$notify.error({
              title: '',
              message: ''
            });
          }else {
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
          }
        });
      }
    },
    computed: {
      ...mapState({
        QuestionnaireTemplate: state => state.QuestionnaireTemplate,
      }),
    }
  }
</script>

<style>
</style>



