<template>
  <div>
    <el-row type="flex" justify="end" align="middle" style="padding: 20px">
      <el-col :span="5" ><el-input v-model="questionnaireName" placeholder=""></el-input></el-col>
      <el-col :span="4" :offset="1">
        <el-select v-model="questionnaireTemplateId" placeholder="">
          <el-option
            v-for="item in this.QuestionnaireTemplate.list"
            :key="item._id"
            :label="item.title"
            :value="item._id">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="2" :offset="1"><el-button type="primary" size="large" style="postio" @click="add"></el-button></el-col>
    </el-row>
    <el-table
      :data="this.QuestionnaireEach.list"
      border
      style="width: 100%;">
      <el-table-column
        label=""
        width="300">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.operation.dateString }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="150">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.operation.userName }}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button
            v-if="scope.row.status"
            type="danger"
            size="small"
            @click="changeStatus(scope.row._id, !scope.row.status)"></el-button>
          <el-button
            v-else
            type="primary"
            size="small"
            @click="changeStatus(scope.row._id, !scope.row.status)"></el-button>
          <router-link :to="{ path: 'statistics', query: { questionnaireId: scope.row._id, questionnaireName: scope.row.title }}">
            <el-button
              size="small"></el-button>
          </router-link>
          <router-link :to="{ path: `questionnaire/${scope.row._id}`}">
            <el-button
              size="small"></el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'SetQuestionnaire',
    data(){
      return {
        questionnaireName: '',
        questionnaireTemplateId: '',
      }
    },
    methods: {
      ...mapActions(['getQuestionnaireEachList', 'getQuestionnaireTemplateList', 'addQuestionnaire', 'changeQuestionnaireEachStatus']),
      init() {
        this.getQuestionnaireEachList();
        this.getQuestionnaireTemplateList();
      },
      add() {
        this.addQuestionnaire({title: this.questionnaireName, templateId: this.questionnaireTemplateId}).then(()=> {
          this.getQuestionnaireEachList();
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
        });
      },
      changeStatus(id, status) {
        this.changeQuestionnaireEachStatus({id, status}).then((data) => {
          if(data) {
            this.getQuestionnaireEachList();
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
          }else {
            this.$notify({
              title: '',
              message: '',
              type: 'warning'
            });
          }

        });
      },

    },
    computed: {
      ...mapState({
        Question: state => state.question,
      }),
    },
    computed: {
      ...mapState({
        QuestionnaireEach: state => state.QuestionnaireEach,
        QuestionnaireTemplate: state => state.QuestionnaireTemplate,
      }),
    },
    mounted() {
      this.init();
    }
  }
</script>

<style>
</style>



