<template>
  <div class="widthHeight100">
    <el-row class="row-padding" type="flex" justify="space-between" align="middle" >
      <el-col :span="5"><el-input v-model="title" placeholder="" @blur="titleInput"></el-input></el-col>
      <el-col :span="5">
        <router-link :to="{ path: 'questionnaire/preview'}">
          <el-button></el-button>
        </router-link>
        <el-button type="success" @click="save"></el-button>
      </el-col>
    </el-row>
    <el-row class="row-padding" type="flex" justify="left" align="top" >
      <el-col :span="24">
        <el-row type="flex" justify="left" align="middle" >
          <h2></h2>
        </el-row>
        <set-answer></set-answer>
      </el-col>
      <el-col :span="18">
        <el-row type="flex" justify="left" align="middle" >
          <h2></h2>
        </el-row>
        <question-list></question-list>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import { mapActions, mapState } from 'vuex';
  import SetAnswer from '../components/SetAnswer';
  import QuestionList from '../components/QuestionList';

  export default {
    name: 'SetQuestion',
    data(){
      return {
        title: ''
      }
    },
    methods: {
      ...mapActions(['setQuestionnaireTitle', 'saveQuestionnaireTemplate', 'clearTemplate', 'getQuestionnaireTemplateById']),
      init() {
        this.bindEvent();
        // 
        if(this.$route.query.questionnaireTemplateId) {
          this.getQuestionnaireTemplateById({id: this.$route.query.questionnaireTemplateId}).then(y => {
            this.title = _.cloneDeep(this.QuestionnaireTemplate.template.title);
          });
        }else {
          this.title = this.QuestionnaireTemplate.template.title;
        }
      },
      bindEvent() {
      },
      titleInput() {
        this.setQuestionnaireTitle({title: this.title});
      },
      save () {
        if(this.validate()) {
          this.saveQuestionnaireTemplate().then((error) => {
            if(error){
              this.showError('');
            }else {
              this.showSuccess('');
              this.clear();
              this.$router.replace({path: '/questionList'});
            }
          });
        }
      },
      validate() {
        if(this.title === '') return this.showWarning('');
        if(this.QuestionnaireTemplate.template.topic.length < 1) return this.showWarning('');
        return true
      },
      clear() {
        this.clearTemplate();
      },
      showWarning(message) {
        this.$notify({
          title: '',
          message: message,
          type: 'warning'
        });
        return false;
      },
      showError(message) {
        this.$notify.error({
          title: '',
          message: message
        });
      },
      showSuccess(message) {
        this.$notify({
          title: '',
          message: message,
          type: 'success'
        });
      }
    },
    components: {
      SetAnswer,
      QuestionList
    },
    computed: {
      ...mapState({
        QuestionnaireTemplate: state => state.QuestionnaireTemplate
      }),
    },
    mounted() {
      this.init();
    }
  }
</script>

<style>
  .row-padding {
    padding-top: 20px;
  }
  .markSetRow {
    padding-bottom: 25px;
  }
  .backNextRow {
    margin-bottom: 10px;
  }
  .widthHeight100 {
    width: 100%;
    height: 100%;
  }

</style>



